.home {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro {
  max-width: 500px;
  text-align: center;
}

.home__img {
  max-width: 180px;
  border-radius: 50%;
  margin-bottom: 1.5rem;
}

.home__name {
  font-size: var(--h1-font-size);
  font-weight: var(--font-bold);
  margin-bottom: 0.5rem;
}

.home__socials {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 1.75rem;
  margin: 1.5rem 0;
}

.home__social-link {
  color: var(--title-color);
  font-size: 1.3rem;
  transition: .3s;
  line-height: 0;
}

.home__social-link:hover {
  color: hsl(43, 100%, 68%);
}

.home__social-link svg {
  height: 20.8px;
  fill: var(--title-color);
  transition: .3s;
  padding-bottom: 1.5px;
}

.home__social-link svg:hover {
  fill: hsl(43, 100%, 68%);
} 

.scroll__down {
  position: absolute;
  bottom: 2.5rem;
  left: 0;
  width: 100%;
}

.home__scroll-name {
  font-size: var(--small-font-size);
}

.mouse {
  border: 2px solid #454360;
  border: 2px solid var(--text-color);
  display: block;
  height: 1.6rem;
  width: 1.25rem;
  margin: auto;
  margin-top: .75rem;
  border-radius: 1rem;
  position: relative;
}

@keyframes ani-mouse {
  0% {
    top: 29%
  }

  15% {
    top: 50%
  }

  50% {
    top: 50%
  }

  1000% {
    top: 29%
  }
}

.wheel {
  background-color: var(--title-color);
  border-radius: 100%;
  width: 0.25rem;
  height: 0.25rem;
  position: absolute;
  top: .5rem;
  left: 50%;
  transform: translateX(-50%);
  animation: ani-mouse 2s linear infinite;
}

.shapes {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

@media screen and (max-width: 1024px) {
  .home__social-link {
    font-size: 1.125rem;
  }
}